
.Game {
  justify-content: center;
}

.MissingNo {
  display: grid;
  grid-template-columns: repeat(11, minmax(0, 1fr));
}

.MissingName {
  box-sizing: border-box;
  display: flex;
  grid-column: span 3/span 3;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border: ghostwhite solid 2px;
  border-radius: 0.25rem;
}

.MissingEach {
  box-sizing: border-box;
  display: flex;
  grid-column: span 1/span 1;
  align-items: center;
  justify-content: center;
  max-width: 3rem;
  max-height: 3rem;
  border: ghostwhite solid 2px;
  border-radius: 0.25rem;
}

.MissingStat {
  box-sizing: border-box;
  display: flex;
  grid-column: span 2/span 2;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border: ghostwhite solid 2px;
  border-radius: 0.25rem;
}
